import "./App.css";
import { Routes, Route, Navigate } from "react-router-dom";
import HomePage from "./components/Page/HomePage";
import Favorites from "./components/Page/Favorites";
import ErrorPage from "./components/Page/404";
import AuthContext from "./store/auth-context";
import { useContext } from "react";
import MyProfile from "./components/Page/MyProfile";
import HomeItemPage from "./components/Page/HomeItemPage";

function App() {
  const ctx = useContext(AuthContext);

  return (
    <Routes>
      <Route path="/" exact element={<HomePage />} />
      <Route
        path="/my-favorites"
        exact
        element={
          ctx.isLoggedInUser.isLoggedIn ? (
            <Favorites />
          ) : (
            <Navigate replace to="/" />
          )
        }
      />
      <Route path="/homes/:id" exact element={<HomeItemPage />} />
      <Route
        path="/my-profile"
        exact
        element={
          ctx.isLoggedInUser.isLoggedIn ? (
            <MyProfile />
          ) : (
            <Navigate replace to="/" />
          )
        }
      />
      <Route path="*" exact element={<ErrorPage />} />
    </Routes>
  );
}

export default App;
